@import url('https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c:wght@100;300;400;500;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300&display=swap');

$mainColor: #FF5530;
$bgColor: #111;
$divColor: #222;
$hoverColor: #ff3d12;
$shadowColor: rgba(255, 85, 48, 0.25);


@mixin flex($justify, $align) {
  display: flex;
  justify-content: $justify;
  align-items: $align;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

// loader
.loading {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.397);
  overflow: hidden;
}

.loader {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  display: grid;
  grid-template-columns: repeat(3, 20px);
  grid-template-rows: repeat(3, 20px);
  grid-gap: 5px;
  justify-items: center;
  align-items: center;
  justify-self: center;
  z-index: 2;
}

.square {
  width: 20px;
  height: 20px;
  border-radius: 3px;
  box-shadow: 0 0 5px $mainColor;
  animation: scaleInOut 1s infinite;
  background-color: $mainColor;
}

@keyframes scaleInOut {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.7);
  }
}